import React, { useState, useEffect } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, ChevronsLeft, ChevronsRight
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, Modal, ModalHeader, ModalBody, ModalFooter
} from 'reactstrap'

import useJwt from '@src/auth/jwt/useJwt'
import InvoiceModal from '../SuperAdminPages/DashboardUI/InvoiceModal'
import { formatReadableDate } from '../../helper'
import DataTable from 'react-data-table-component'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import { Error, Success, ErrorMessage } from '../../viewhelper'
import { CURRENCY_SYMBOL } from '../../Configuarables'
import { useHistory } from 'react-router-dom'
import CommonDataTable from '../CommonDataTable'
import ImgUpload from '../ImgUpload'
import { toast } from 'react-toastify'

const Invoices = () => {
    const [FilteredInvoices, setFilteredInvoices] = useState([])
    const [InvModal, setInvModal] = useState(false)
    const [reset, setReset] = useState(false)
    const [UploadModal, setUploadModal] = useState(false)
    const [InvoiceDetails, setInvoiceDetails] = useState({})
    const [tableDataLoading, setTDL] = useState(true)
    const [invoice_id, setinvoice_id] = useState(null)

    const [imgUpLoading, setimgUpLoading] = useState(false)
    const [fileUrls, setFileUrls] = useState([])
    const [isLoading, setLoading] = useState(false)

    const [state, setState] = useState({
        previewVisible: false,
        previewImage: '',
        previewTitle: '',
        fileList: []
    })
    const column = [
        {
            name: 'Invoice ID',
            maxWidth: '120px',
            // sortable: true,
            selector: 'invoice_id'
        },
        {
            name: 'Invoices Issue Date',
            minWidth: '220px',
            // sortable: true,
            selector: row => formatReadableDate(row.issue_date)
        },
        {
            name: 'Total Amount',
            minWidth: '150px',
            // sortable: true,
            selector: row => `${CURRENCY_SYMBOL} ${row.total_amount}`
        },
        {
            name: 'Paid Amount',
            minWidth: '150px',
            // sortable: true,
            selector: row => `${CURRENCY_SYMBOL} ${row.pay_amount}`
        },
        {
            name: 'Status',
            minWidth: '100px',
            // sortable: true,
            selector: row => {
                const statusBG = {
                    2: { title: 'Paid', color: 'success' },
                    14: { title: 'Due', color: 'warning' },
                    15: { title: 'Partial pay', color: 'light-warning' },
                    16: { title: 'Pending approval', color: 'light-success' }
                }
                return <Badge color={statusBG[row.statusType.id].color} pill className='px-1'>
                    {statusBG[row.statusType.id].title}
                </Badge>
            }
        },
        {
            name: 'Money Receipt',
            minWidth: '150px',
            // sortable: true,
            selector: row => <Button.Ripple size='sm' color='primary' type='button' onClick={e => {
                setinvoice_id(row.invoice_id)
                setUploadModal(true)
            }}>
                Upload
            </Button.Ripple>
        },
        // {
        //     name: 'View',
        //     minWidth: '50px',
        //     // sortable: true,
        //     selector: row => <span title="View">
        //         <Eye size={18}
        //             color='seagreen'
        //             style={{ cursor: 'pointer' }}
        //             onClick={(e) => {
        //                 setInvoiceDetails(row)
        //                 setInvModal(true)
        //             }}
        //         />
        //     </span>
        // },
        {
            name: 'Invoice PDF',
            minWidth: '150px',
            // sortable: true,
            selector: row => <a href={row.invoice_url} download>
                <Button.Ripple size='sm' color='primary' type='button'>
                    Download
                </Button.Ripple>
            </a>
        }
    ]

    useEffect(() => {
        useJwt.GetBranchManagerInvoice().then(res => {
            console.log("GetBranchManagerInvoice", res.data.payload)
            setFilteredInvoices(res.data.payload)
            setTDL(false)
        }).catch(error => {
            console.log("GetBranchManagerInvoice", error)
            setTDL(false)
        })
    }, [reset])
    const handleSubmit = e => {
        if (imgUpLoading) {
            toast.error('please wait, image is uploading to the server')
            return 0
        }
        if (!fileUrls.length) {
            return 0
        }
        const payment_attachment = fileUrls[0].url

        setLoading(true)
        useJwt.submitPaymentAttachment({ payment_attachment, invoice_id }).then(res => {
            Success(res)
            setLoading(false)
            setUploadModal(false)
            setReset(!reset)
        }).catch(err => {
            Error(err)
            setLoading(false)
        })
    }
    return (
        <>
            {UploadModal && <Card>
                <CardHeader className='border-bottom titleSelect'>
                    <CardTitle tag='h4'>Upload Money Receipt</CardTitle>
                </CardHeader>
                <CardBody className='mt-1' >
                    {/* <Label>Photo</Label> */}
                    <ImgUpload
                        setimgUpLoading={setimgUpLoading}
                        state={state}
                        setState={setState}
                        fileUrls={fileUrls}
                        setFileUrls={setFileUrls} />

                    <Button.Ripple size='sm' disabled={isLoading || imgUpLoading} className='mt-1 ml-1' color='primary' onClick={handleSubmit}>
                        {!isLoading ? 'Submit' : 'Loading'}
                    </Button.Ripple>
                </CardBody>
            </Card >}
            <Card>
                <CardHeader className='border-bottom titleSelect'>
                    <CardTitle tag='h4'>Invoices</CardTitle>
                </CardHeader>
                <CardBody>
                    <Row>
                        <Col md='12'>
                            <CommonDataTable column={column} TableData={FilteredInvoices} TableDataLoading={tableDataLoading} />
                        </Col>
                    </Row>
                </CardBody>
                {/* {Object.keys(InvoiceDetails).length ? <InvoiceModal InvoiceDetails={InvoiceDetails} InvModal={InvModal} setInvModal={setInvModal} /> : ''} */}

            </Card >
        </>
    )
}

export default Invoices